        background-image: url(../images/program/desk-program3@2x.png);
      }
    }
  }

  .program__information3 {
    &::before {
      content: '';
      display: block;
      width: 88px;
      height: 97px;
      background-image: url(../images/program/desk-program2.png);
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      background-size: 88px 97px;

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/program/desk-program2@2x.png);
      }
    }
  }

  .program__information4 {
    &::before {
      content: '';
      display: block;
      width: 83px;
      height: 92px;
      background-image: url(../images/program/desk-program4.png);
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);

      background-size: 83px 92px;

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/program/desk-program4@2x.png);
      }
    }
  }
}
